<template>
  <div>
    <el-card class="">
      <div slot="header" class="clearfix">
        <span>菜单权限</span>
      </div>
      <el-alert title="Tips" type="warning" description="设置后台管理系统的左侧菜单，目前至多支持两层菜单，三层以上菜单将不会显示。" show-icon
                :closable="false">
      </el-alert>
      <!-- 树形组件 -->
      <el-table :data="allMenus" row-key="permission_id" border stripe default-expand-all class="am-margin-top">
        <el-table-column label="资源名称" width="200" fixed="left">
          <template slot-scope="scope">
            <template v-if="scope.row.resource_type_id === 2">
              <span>{{ scope.row.menu_name }}</span>
            </template>
            <template v-if="scope.row.resource_type_id === 3">
              <span>{{ scope.row.permission_remark }}</span>
            </template>
          </template>
        </el-table-column>
        <el-table-column label="图标">
        </el-table-column>
        <el-table-column label="资源类型">
          <template slot-scope="scope">
            <template v-if="scope.row.resource_type_id === 2">
              <el-tag>{{ scope.row.resource_type_name }}</el-tag>
            </template>
            <template v-if="scope.row.resource_type_id === 3">
              <el-tag type="warning">{{ scope.row.resource_type_name }}</el-tag>
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="resource_type_code" label="资源代码" width="100">
        </el-table-column>
        <el-table-column prop="permission_code" label="权限代码" width="130">
        </el-table-column>
        <el-table-column prop="operation_name" label="操作名称" width="70">
        </el-table-column>
        <el-table-column prop="route_name" label="路由名称" width="110">
        </el-table-column>
        <el-table-column prop="route_full_path" label="完整路由地址" width="150">
        </el-table-column>
        <el-table-column prop="menu_order" label="菜单序号">
        </el-table-column>
        <el-table-column prop="permission_remark" label="备注" width="180">
        </el-table-column>
        <el-table-column label="操作" width="230" fixed="right">
          <template slot-scope="scope">
            <el-button @click="openEditDialog(scope.row)" :disabled="scope.row.permission_id === 0" icon="el-icon-edit"
                       type="primary" title="编辑" plain>
            </el-button>
            <el-button @click="openInsertDialog(scope.row.permission_id, scope.row)" icon="el-icon-circle-plus-outline"
                       type="success" title="添加" plain>
            </el-button>
            <el-button :disabled="scope.row.permission_id === 0" icon="el-icon-setting" type="warning" title="设置图标"
                       plain>
            </el-button>
            <el-button @click="handleRemoveRoute(scope.row)" :disabled="scope.row.permission_id === 0"
                       icon="el-icon-delete" type="danger" title="删除" plain>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 添加Dialog -->
      <el-dialog title="添加菜单/操作按钮" :visible.sync="insert_dialog_visible" @closed="handleClosedDialog('insert_form')">
        <el-tabs type="card">
          <el-tab-pane label="菜单">
            <el-form ref="insert_form" :model="insert_form" :rules="rules" label-width="120px">
              <el-form-item label="菜单名称" prop="menu_name">
                <el-input v-model="insert_form.menu_name"></el-input>
              </el-form-item>
              <el-form-item label="是否链接跳转">
                <el-switch v-model="insert_form.is_link" active-color="#13ce66" inactive-color="#ff4949">
                </el-switch>
              </el-form-item>
              <el-form-item label="关联路由" v-show="insert_form.is_link">
                <el-cascader v-model="insert_form.route_id" :options="treeRoutes"
                             :props="{ value: 'route_id',label: 'route_name',emitPath:false }" placeholder="指定此菜单对应的路由">
                </el-cascader>
              </el-form-item>
              <el-form-item label="路由完整路径" v-show="insert_form.is_link">
                <el-input v-model="insert_form.route_full_path" placeholder="指定此菜单的链接地址"></el-input>
              </el-form-item>
              <el-form-item label="显示顺序" prop="menu_order">
                <el-input v-model="insert_form.menu_order" placeholder="显示顺序按照数字从小到大，如2001"></el-input>
              </el-form-item>
              <el-form-item label="菜单备注" prop="menu_order">
                <el-input v-model="insert_form.permission_remark" placeholder="菜单备注信息"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="操作按钮">配置管理</el-tab-pane>
        </el-tabs>

        <div slot="footer" class="dialog-footer">
          <el-button @click="insert_dialog_visible = false">取 消</el-button>
          <el-button type="primary" @click="handleInsertMenu">添 加</el-button>
        </div>
      </el-dialog>
      <!-- 编辑Dialog -->
      <el-dialog title="编辑节点" :visible.sync="edit_dialog_visible" @closed="handleClosedDialog('edit_form')">
        <el-form ref="edit_form" :model="edit_form" :rules="rules" label-width="80px" :label-position="'left'">
          <el-form-item label="菜单名称" prop="name">
            <el-input v-model="edit_form.name"></el-input>
          </el-form-item>
          <el-form-item label="组件名称">
            <el-input v-model="edit_form.component" placeholder="指定此菜单对应的组件名称"></el-input>
          </el-form-item>
          <el-form-item label="链接地址">
            <el-input v-model="edit_form.path" placeholder="指定此菜单的链接地址，选填"></el-input>
          </el-form-item>
          <el-form-item label="显示顺序" prop="menu_order">
            <el-input v-model="edit_form.menu_order" placeholder="显示顺序按照数字从小到大，如2001"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="edit_dialog_visible = false">取 消</el-button>
          <el-button type="primary" @click="handleEditMenu">保 存</el-button>
        </div>
      </el-dialog>
      <!-- 选择图标Dialog -->
      <el-dialog title="选择图标" width="72%" :visible.sync="icon_modal_visible">
        <!-- 图标列表 -->
        <ul class="icon-list">
          <li v-for="item in icons" :key="item.id" :class="{ checked: item.checked }" @click="handleCheckIcon(item)">
            <span>
              <i :class="`el-icon-${item.name}`"></i>
              <span class="icon-name">el-icon-{{ item.name }}</span>
            </span>
          </li>
        </ul>
        <!-- 分页器 -->
        <el-pagination background layout="->,prev, pager, next" :page-size="pageSize" :total="total_icon"
                       @current-change="handlePageChange"></el-pagination>
        <div slot="footer" class="dialog-footer">
          <el-button @click="icon_modal_visible = false">取 消</el-button>
          <el-button type="primary" @click="handleUpdateIcon">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { Menu, Icon } from "@/api/";

export default {
  name: "Menu",
  data() {
    return {
      // 选择图标
      icon_modal_visible: false,
      icons: [],
      pageSize: 30,
      total_icon: 0,
      checked_icon: { icon_id: '', icon: '' },
      // 当前操作节点
      current_node: '',
      // 添加Dialog
      insert_dialog_visible: false,
      insert_form: {
        menu_name: '',
        is_link: true,
        menu_order: '',
        route_id: '',
        route_full_path: '',
        parent_id: '',
        icon_id: '',
        permission_remark: '',
      },
      // 编辑Dialog
      edit_dialog_visible: false,
      edit_form: {
        menu_name: '',
        menu_order: '',
        route_id: '',
        route_full_path: '',
        parent_id: '',
        icon_id: '',
        permission_remark: '',
      },
      rules: {
        menu_name: [{ required: true, type: 'string', message: '请输入菜单名称！', trigger: 'blur' }],
        menu_order: [
          { required: true, type: 'string', message: '请填写显示顺序！', trigger: 'blur' },
          { pattern: /^\d+$/, message: '顺序必须是数字组成！', trigger: 'blur' }
        ],
        route_id: [{ message: "请选择与菜单相关联的路由", trigger: 'change' }]
      }
    }
  },
  created() {
    document.title = "菜单权限";
    this.loadIcons(1);
    // 加载菜单列表
    this.loadMenuList({ type: 'flat' });
  },
  computed: {
    ...mapGetters('Route', ['treeRoutes', 'route_full_path']),
    ...mapGetters('Menu', ['allMenus']),
  },
  watch: {
    "insert_form.route_id"(route_id) {
      this.insert_form.route_full_path = this.route_full_path(route_id);
    },
  },
  methods: {
    // 加载所有 action
    ...mapActions('Menu', ['loadMenuList', 'createMenu', 'editMenu', 'removeMenu']),
    // 打开添加Dialog获取pId
    openInsertDialog(parent_id, menu) {
      // 缓存父级id
      this.insert_form.parent_id = parent_id;
      // 缓存当前操作行的指针
      this.current_route = menu;
      // 打开弹窗
      this.insert_dialog_visible = true;
    },
    // 加载element图标
    async loadIcons(pageIndex) {
      let { status, data, total } = await Icon.list({ pageSize: this.pageSize, pageIndex });
      if (status) {
        // 添加选择状态--布尔值
        data.forEach((item) => {
          item.checked = false
        });
        this.icons = data;
        this.total_icon = total;
      }
    },
    // 分页器改变页码数
    handlePageChange(pageIndex) {
      this.loadIcons(pageIndex);
    },
    // 打开图标选择Dialog
    openIconDialog(node, data) {
      this.icon_modal_visible = true;
      // 转存node
      this.current_node = node;
    },
    // 选中图标
    handleCheckIcon(data) {
      this.icons.forEach((item) => {
        // 切换选中状态
        item.checked = item === data ? !data.checked : false;
      });
      // 存储/取消选中的iconName
      this.checked_icon.icon_id = data.checked ? data.id : '';
      this.checked_icon.icon_name = data.checked ? data.name : '';
    },
    // 更新图标
    async handleUpdateIcon() {
      let { id } = this.current_node.data;
      let { status, msg } = await Menu.setIcon(id, { ...this.checked_icon });
      if (status) {
        // 信息提示
        this.$message.success(msg);
        // 关闭Dialog
        this.icon_modal_visible = false;
        // 修改图标
        this.current_node.data.icon_name = this.checked_icon.icon_name;
      }
    },
    // 删除节点
    handleRemoveRoute() {
      this.$confirm('此操作将永久删除该菜单, 是否继续?', { type: 'warning', })
          .then(async () => {
            this.setDefaultExpandedKeys()
            let { status, msg } = await Menu.remove(data.id);
            if (status) {
              // 原型方法remove
              node.remove();
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          })
          .catch(() => {
            this.$message.info('已取消删除');
          });
    },
    // 确认添加节点
    async handleInsertMenu() {
      this.$refs.insert_form.validate(async (valid) => {
        if (valid) {
          //缓存默认展开节点
          this.setDefaultExpandedKeys();
          let { status, msg, data } = await Menu.insert({ ...this.insert_form });
          if (status) {
            // 树形结构添加子节点
            this.$refs.tree.append({ ...this.insert_form, ...data }, this.current_node);
            // 信息提示
            this.$message.success(msg);
            // 关闭Dialog
            this.insert_dialog_visible = false;
          } else {
            this.$message.error(msg);
          }
        }
      })
    },
    // 打开编辑Dialog
    openEditDialog(node, data) {
      this.edit_dialog_visible = true;
      //转存节点data数据
      this.edit_form = { ...data };
      // 转存node节点
      this.current_node = node;
    },
    // 编辑节点
    async handleEditMenu() {
      let { id } = this.edit_form;
      this.$refs.edit_form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await Menu.update(id, { ...this.edit_form });
          if (status) {
            // 更新节点
            this.current_node.data = { ...this.edit_form }
            // 信息提示
            this.$message.success(msg);
            // 关闭Dialog
            this.edit_dialog_visible = false;
          } else {
            this.$message.error(msg);
          }
        }
      })
    },
    // 关闭dialog动画结束之后，清除之前的验证提示
    handleClosedDialog(formName) {
      this.$refs[formName].resetFields();
    },

  }
}
</script>

<style lang="less" scoped>
.node-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;

  .node-order {
    width: 100px;
    display: inline-block;
    font-size: 12px;
  }
}

ul.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0 !important;
  border: solid 1px #eaeefb;
  border-radius: 4px;
}

.icon-list li {
  float: left;
  width: 10%;
  text-align: center;
  height: 100px;
  line-height: 100px;
  color: #666;
  font-size: 10px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-right: -1px;
  margin-bottom: -1px;

  &::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }

  span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", SimSun, sans-serif;
    color: #99a9bf;
    transition: color 0.15s linear;
  }

  i {
    display: block;
    font-size: 26px;
    margin-bottom: 15px;
    color: #606266;
    transition: color 0.15s linear;
  }

  .icon-name {
    display: inline-block;
    padding: 0 3px;
    height: 1em;
  }

  &.checked,
  &.checked:hover {
    background-color: rgb(92, 182, 255);

    span,
    i {
      color: white;
    }
  }

  &:hover {

    span,
    i {
      color: rgb(92, 182, 255);
    }
  }
}
</style>
